<template>
  <div class="container store-rpt-wrapper dept-wrapper">
    <div v-if="loading" class="loading-overlay">
      <div class="overlay-content"><img src="@/assets/img/loading.gif" style="width: 60px" /></div>
    </div>
    <Header />
    <div class="content-wrapper">
      <div class="filter-toggle-wrapper" ref="toggleFilter">
        <span>
          <button class="filter-toggle" @click="toggleFilter()" :class="isFilterActive ? 'collasped' : ''"><fa :icon="['fas', 'sliders']"></fa></button>
        </span>
        <div class="filter-wrapper" :class="[isFilterActive ? 'slide-in' : 'slide-out']">
          <div class="filter-content-wrapper">
            <dropdown class="filter-popup-wrapper stores-popup">
              <template #header>
                <button class="filter-btn"><!--<fa icon="store" />--> <span id="store-selection">Stores</span><fa icon="angle-down" /></button>
              </template>
              <template #content>
                <div class="stores-selection-wrapper">
                  <div class="checkbox-wrapper stores-filter-wrapper">
                    <label for="all-stores">
                      <input type="checkbox" name="store_type" id="all-stores" v-model="selectAllCompanies" value="all" @change="selectedStore" />
                      <span class="cbx">
                        <svg width="12px" height="11px" viewBox="0 0 12 11">
                          <polyline points="1 6.29411765 4.5 10 11 1"></polyline>
                        </svg>
                      </span>
                      <span>All</span>
                    </label>
                    <label :for="company.id" v-for="company in companies" :key="company.id">
                      <input type="checkbox" name="store_type" :id="company.id" :value="company" v-model="selectedCompanies" @change="selectedStore" />
                      <span class="cbx">
                        <svg width="12px" height="11px" viewBox="0 0 12 11">
                          <polyline points="1 6.29411765 4.5 10 11 1"></polyline>
                        </svg>
                      </span>
                      <span class="compan-name">{{ company.companyName }}</span>
                    </label>
                  </div>
                </div>
              </template>
            </dropdown>
            <popup class="filter-popup-wrapper">
                <template #header>
                  <button class="filter-btn">
                    <span><!--<fa :icon="['fas', 'sliders']" />--><span id="display-selection">Daily</span></span><fa icon="angle-down" />
                  </button>
                </template>
                <template #content>
                  <div class="display-selection-wrapper">
                    <span>
                      <input type="radio" name="display_type" value="D" v-model="displayType" @change="selectedDisplayType" id="display-daily" />
                      <fa class="checkmark" icon="check" />
                      <label for="display-daily">Daily</label>
                    </span>
                    <span>
                      <input type="radio" name="display_type" value="W" v-model="displayType" @change="selectedDisplayType" id="display-weekly" />
                      <fa class="checkmark" icon="check" />
                      <label for="display-weekly">Weekly</label>
                    </span>
                    <span>
                      <input type="radio" name="display_type" value="M" v-model="displayType" @change="selectedDisplayType" id="display-monthly" />
                      <fa class="checkmark" icon="check" />
                      <label for="display-monthly">Monthly</label>
                    </span>
                    <span>
                      <input type="radio" name="display_type" value="Y" v-model="displayType" @change="selectedDisplayType" id="display-yearly" />
                      <fa class="checkmark" icon="check" />
                      <label for="display-yearly">Yearly</label>
                    </span>
                  </div>
                </template>
              </popup>
              <div class="filter-header">
                <div class="calendar-wrapper">
                  <DatePicker 
                    @changeDateValue="startDate = $event"
                    :weeklyCalendar="displayType == 'W'"
                    :monthlyCalendar="displayType == 'M'"
                    :yearlyCalendar="displayType == 'Y'"
                    :defaultDate="startDate"
                    :dateRange="dateRange"
                    labelEnableText="Date" />
                </div>
                <button class="search-btn btn btn-square" @click="searchWithFilter"><fa icon="magnifying-glass"></fa></button>
              </div>
          </div>
        </div>
      </div>
      <div class="content" v-bind:style="{ height: 'calc(' + windowHeight + 'px - 90px)' }">
        <div v-if="!empty && mkts.mktlist.length <= 0" class="no-results"><fa :icon="['fas', 'magnifying-glass']" />No search results found</div>
        <div v-if="!empty && mkts.mktlist.length > 0" class="dept-table-wrapper">
          <DataTable v-if="!storesDetail" id="rpt-table" class="stores-tb" :value="mkts.mktlist" :exportFilename="fileName" ref="dt">
            <template #header>
              <div class="table-header-wrapper">
                <div class="table-header">
                  <h3>{{ $t('header.' + this.$route.name) }}</h3>
                  <p v-if="this.endDate">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                  <p v-if="!this.endDate">As of date {{ formatDate(rptStartDate) }}</p>
                </div>
                <div class="table-btn-group">
                  <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print"></fa></button>
                  <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-csv"></fa></button>
                </div>
              </div>
            </template>
            <Column field="companyName" header="Stores">
              <template #body="slotProps">
                <a @click="getSalesDetail(slotProps.data.mktid, slotProps.data.comp_name, slotProps.data.total)">{{ getName(slotProps.data.mktid) }}</a>
              </template>
            </Column>
            <Column field="total" header="Sale Amount($)" class="amount">
              <template #body="slotProps">
                <a @click="getSalesDetail(slotProps.data.mktid, slotProps.data.comp_name, slotProps.data.total)">{{ formatPrice(slotProps.data.total) }}</a>
              </template>
            </Column>
            <ColumnGroup type="footer">
              <Row>
                <Column footer="Total Sale Amount($)" footerStyle="text-align:right" />
                <Column :footer="formatPrice(storesTotalSale)" footerStyle="text-align:right" />
              </Row>
            </ColumnGroup>
          </DataTable>
        </div>
        <div v-if="storesDetail">
          <button class="back-btn" @click="back()"><fa :icon="['fas', 'arrow-left']" /></button>
          <div class="dept-table-wrapper">
            <DataTable id="rpt-table" class="dept-tb store-detail" :value="depts" :exportFilename="fileName" ref="dt">
              <template #header>
                <div class="table-header-wrapper">
                  <div class="table-header">
                    <h3>Department Sales Report - {{ storeName }}</h3>
                    <p v-if="this.endDate">As of date {{ formatDate(rptStartDate) }} - {{ formatDate(rptEndDate) }}</p>
                    <p v-if="!this.endDate">As of date {{ formatDate(rptStartDate) }}</p>
                  </div>
                  <div class="table-btn-group">
                    <button class="print-btn btn btn-circle" onclick="window.print()"><fa icon="print" /></button>
                    <button class="excel-btn btn btn-circle" @click="exportCSV($event)"><fa icon="file-csv" /></button>
                  </div>
                </div>
              </template>
              <Column field="dep_name" header="Department">
                <template #body="slotProps">{{ slotProps.data.dep_no + " " + slotProps.data.dep_name }}</template>
              </Column>
              <Column field="amount" header="Amount ($)" class="amount">
                <template #body="slotProps">{{ formatPrice(slotProps.data.amount) }}</template>
              </Column>
              <Column field="percentage" header="%" class="percentage">
                <template #body="slotProps">{{ formatPrice(slotProps.data.percentage) }}%</template>
              </Column>
              <ColumnGroup type="footer">
                <Row>
                  <Column footer="Total" footerStyle="text-align:right" />
                  <Column :footer="formatPrice(deptsTotal)" footerStyle="text-align:right" />
                  <Column footer="" />
                </Row>
              </ColumnGroup>
            </DataTable>
            <div class="dept-total store-detail">
              <ul>
                <li class="bold-600">Department Total($): <span class="bold-600">{{ formatPrice(deptsTotal) }}</span></li>
                <li>Member & VIP Discount($): <span>-{{ formatPrice(deptsDiscount) }}</span></li>
                <li>Taxes($): <span>{{ formatPrice(deptsTax) }}</span></li>
                <li>Total Accounted For($):<span class="bold-600">{{ formatPrice(deptsTotal - deptsDiscount + deptsTax) }}</span></li>
              </ul>
              <div class="doughnut-chart" v-if="depts.length > 0">
                <VueApexCharts :options="setDoughnutChartOptions" :series="deptsAmount" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import { reactive } from "vue";
import axios from "axios";
import moment from "moment";
import Header from "@/components/Header.vue";
import Popup from "@/components/Popup.vue";
import Dropdown from "@/components/Dropdown.vue";
import DatePicker from "@/components/DatePicker.vue";

export default {
  components: {
    Header,
    Popup,
    Dropdown,
    DatePicker
  },
  setup() {
    const state = reactive({
      expandedRows: {},
    });

    const onExpandRow = (id) => {
      const checkProp = id in state.expandedRows;
      if (checkProp) {
        delete state.expandedRows[id];
      } else {
        state.expandedRows[id] = true;
      }
      state.expandedRows = { ...state.expandedRows };
    };

    return {
      state,
      onExpandRow,
    };
  },
  data() {
    return {
      windowHeight: window.innerHeight,
      isFilterActive: true,
      empty: true,
      storeName: localStorage.getItem("StoreName"),
      loading: false,
      displayType: "D",
      mkts: [],
      depts: [],
      deptsId: [],
      deptsAmount: [],
      deptsDisplay: [],
      deptsTax: "",
      deptsTotal: "",
      startDate: "",
      endDate: "",
      rptStartDate: "",
      rptEndDate: "",
      stn: "",
      cashier: "",
      companies: [
        {
        id: 1027,
        companyName: "Apple Supermarket 1"
        },
        {
        id: 1221,
        companyName: "Apple Supermarket 2"
        },
      ],
      selectedCompanies: "",
      searchStores: [],
      storesDetail: false,
      invalidSearch: false,
      dateRange: false
    };
  },
  mounted() {
    // this.companies = JSON.parse(localStorage.getItem("companies"));

    var selection = document.querySelector("#store-selection");
    var value = "All stores selected";
    selection.innerHTML = value;

    const today = moment();
    this.startDate = moment(today.startOf("day")).set("date",today.get("date") - 1).toDate();
    this.rptStartDate = this.startDate;
    this.rptEndDate = this.startDate;
    this.selectedCompanies = this.companies

    var result = [];

    for (let i = 0; i < this.selectedCompanies.length; i++) {
      result.push( this.selectedCompanies[i].id)
    }
    
    this.filterData = {
      sDate: this.rptStartDate,
      eDate: this.rptEndDate,
      mktids: result
    }

    axios
      .post("https://cloudbackofficereportapidev.azurewebsites.net/Report/StoreSales", this.filterData, {
        headers: {
          marketid: result[0],
        },
      })
      .then((response) => {
        this.mkts = response.data.datas;
      })
      .catch((error) => console.log(error.response))
      .finally(() => {
        this.loading = false;
        this.empty = false;
      })

    this.$nextTick(() => {
      window.addEventListener("resize", this.onResize);
    });
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    toggleFilter() {
      this.isFilterActive = !this.isFilterActive;
    },
    formatPrice(value) {
      if (!value || value == 0) {
        return "0.00";
      }
      let val = (value / 1).toFixed(2).replace(",", ".");
      return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatNumber(value) {
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    formatDate(date) {
      return moment(date).format("MMM DD, yyyy");
    },
    exportCSV() {
      this.$refs.dt.exportCSV();
    },
    selectedStore() {
      var value = "Nothing selected";
      var selection = document.querySelector("#store-selection");

      if (this.selectedCompanies.length == 1) {
        value = this.selectedCompanies[0].companyName;
      } else if (this.selectedCompanies.length > 1 && !this.selectAllCompanies) {
        value = this.selectedCompanies.length + " stores selected";
      } else if (this.selectAllCompanies) {
        value = "All stores selected";
      }
      selection.innerHTML = value;
    },
    getName(id) {
      if (id == "1027") {
        return "Apple Supermarket 1"
      } else if(id == "1221") {
        return "Apple Supermarket 2"
      }
    },
    selectedDisplayType() {
      if (this.displayType == "D") {
        this.startDate =  "";
      }
      if (this.displayType == "W") {
        let curr = new Date;
        let first = curr.getDate() - curr.getDay(); 
        let last = first + 6; 
        this.startDate = [new Date(curr.setDate(first)), new Date(curr.setDate(last))]
      }
      if (this.displayType == "M"){
        this.startDate = {month: new Date().getMonth(), year: new Date().getFullYear()}
      }
      if (this.displayType == "Y"){
        this.startDate = new Date().getFullYear();
      }

      var value = "Nothing selected",
        selected = document.querySelector('input[name="display_type"]:checked'),
        selection = document.querySelector("#display-selection");
      if (selected) {
        value = document.querySelector('label[for="' + selected.id + '"]').innerHTML;
      }
      selection.innerHTML = value;
    },
    searchWithFilter() {
      this.storesDetail = false;
      
      if (this.selectedCompanies.length > 0) {
        this.loading = true;
        this.isFilterActive = false;
        this.depts = [];

        if (this.displayType == "D") {
          this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
          this.endDate = "";
          this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
        }
        if (this.displayType == "W") {
          this.rptStartDate = this.startDate[0];
          this.endDate = this.startDate[1];
          this.rptEndDate = this.endDate;
        }
        if (this.displayType == "M"){
          this.rptStartDate = new Date(this.startDate.year, this.startDate.month, 1);
          this.endDate = new Date(this.startDate.year, this.startDate.month + 1, 0);
          this.rptEndDate = this.endDate;
        }
        if (this.displayType == "Y"){
          this.rptStartDate = new Date(this.startDate, 0, 1);
          this.endDate = new Date(this.startDate, 11, 31);
          this.rptEndDate = this.endDate;
        }

        // if (this.startDate) {
        //   this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
        //   this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
        // } else {
        //   this.rptStartDate = "";
        //   this.rptEndDate = "";
        // }

        var result = [];
        for (let i = 0; i < this.selectedCompanies.length; i++) {
          result.push( this.selectedCompanies[i].id)
        }
        
        this.filterData = {
          sDate: this.rptStartDate,
          eDate: this.rptEndDate,
          mktids: result
        }

        axios
          .post("https://cloudbackofficereportapidev.azurewebsites.net/Report/StoreSales", this.filterData, {
            headers: {
              marketid: result[0],
            },
          })
          .then((response) => {
            this.mkts = response.data.datas;
          })
          .catch((error) => console.log(error.response))
          .finally(() => {
            this.loading = false;
            this.empty = false;
          })
      } else {
        this.invalidSearch = true;
      }
    },
    getSalesDetail(id, name, total) {
      if (total > 0 ) {
        this.storesDetail = true;
      }
      this.loading = true
      this.depts = [];
      this.storeName = this.getName(id);

      // if (this.startDate) {
      //   this.rptStartDate = moment(this.startDate).format("YYYY-MM-DD");
      //   this.rptEndDate = moment(this.startDate).format("YYYY-MM-DD");
      // } else {
      //   this.rptStartDate = "";
      //   this.rptEndDate = "";
      // }

      axios
        .get("https://cloudbackofficereportapidev.azurewebsites.net/Report/Department", {
          headers: {
            marketid: id,
          },
          params: {
            sDate: this.rptStartDate,
            eDate: this.rptEndDate,
          },
        })
        .then((response) => {
          this.depts = response.data.datas.deptSummaries;
          this.deptsTax = response.data.datas.tax;
          this.deptsTotal = response.data.datas.total;

          for (let i = 0; i < this.depts.length; i++) {
            this.deptsId[i] = response.data.datas.deptSummaries[i].dep_no;
            this.deptsDisplay[i] = response.data.datas.deptSummaries[i].dep_name;
            this.deptsAmount[i] = response.data.datas.deptSummaries[i].amount;
          }
        })
        .catch((error) => console.log(error.response))
        .finally(() => {
          this.loading = false;
          this.empty = false;
        });
    },
    back() {
      this.storesDetail = false;
    },
  },
  computed: {
    selectAllCompanies: {
      get: function () {
        return this.companies ? this.selectedCompanies.length == this.companies.length : false;
      },
      set: function (value) {
        var selected = [];
        if (value) {
          this.companies.forEach(function (company) {
            selected.push(company);
          });
        }
        this.selectedCompanies = selected;
      },
    },
    fileName() {
      let excelName = this.$route.name + " " + this.startDate + "-" + this.endDate;
      return excelName;
    },
    deptsDiscount() {
      let total = 0;
      for (let dept of this.depts) {
        total += dept.discount;
      }
      return total;
    },
    storesTotalSale() {
      return this.mkts.total;
    },
    setDoughnutChartOptions() {
      return {
        chart: {
          type: "donut",
          fontFamily: 'Inter, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif',
        },
        labels: this.deptsDisplay,
        colors: ["#72f9b4", "#07e2f6", "#00baff", "#c6e430", "#2ad4e3", "#e329e2", "#e6a4f0", "#8964e8", "#aaa0ff", "#c4e328", "#00bdf6", "#fd9106", "#efefef", "#2c990f"],
        legend: {
          position: "bottom",
          floating: true,
        },
        responsive: [
          {
            breakpoint: 1320,
            options: {
              legend: {
                // position: "right",
                floating: false,
              },
              chart: {
                height: 1000,
              },
            },
          },
        ],
        dataLabels: {
          formatter: function (val) {
            return val.toFixed(2) + "%";
          },
          style: {
            fontSize: "15px",
          },
        },
        tooltip: {
          enabled: false,
        },
        states: {
          hover: {
            filter: {
              type: "none",
            },
          },
        },
        plotOptions: {
          pie: {
            expandOnClick: false,
            donut: {
              size: "55%",
              labels: {
                show: true,
                value: {
                  fontSize: "16px",
                },
                total: {
                  show: true,
                  label: "Department Total",
                  color: "#373d3f",
                  formatter: function (w) {
                    return w.globals.seriesTotals
                      .reduce((a, b) => {
                        return a + b;
                      }, 0)
                      .toFixed(2)
                      .toString()
                      .replace(/\B(?=(\d{3})+(?!\d))/g, ",");
                  },
                },
              },
            },
          },
        },
      };
    },
  },
};
</script>
