<template>
  <div ref="customPopup">
    <div :class="showPopup === false ? 'close' : 'open'" @click="toggle">
      <slot name="header" />
    </div>
    <Transition name="expand">
      <div class="popup-content" v-if="showPopup" @click.stop>
        <div>
          <slot name="content" />
        </div>
      </div>
    </Transition>
  </div>
</template>

<script setup>
import { onMounted, onUnmounted, ref } from "vue";

const showPopup = ref(false);
const customPopup = ref(null);

const toggle = () => {
  showPopup.value = !showPopup.value;
};

const handleClickOutside = (event) => {
  if (customPopup.value && !customPopup.value.contains(event.target)) {
    showPopup.value = false;
  }
};

onMounted(() => {
  document.addEventListener("click", handleClickOutside);
});

onUnmounted(() => {
  document.removeEventListener("click", handleClickOutside);
});
</script>
